import React, { Component } from 'react';
import {Link} from "react-router-dom";
import styled from 'styled-components';
import {ButtonNav, ButtonContainer} from './Button';
import Title from './Title';

export default class BottomBar extends Component {
    render(){
        return(
            <FooterWrapper sticky="bottom">
            <div style={{ float: "left" }}>© 2020 Copyright: Kim Nguyen</div>
            <div style={{ float: "right", marginLeft: "auto" }}>
            <span className="mr-2">
                <Link to="/about">
                    <Title name= "About"/>
                    
                    </Link> <Link to="/about"><Title name= "Kimirooni"/></Link>
                    </span>
            </div>
            
        </FooterWrapper>
        );
    }
}

const FooterWrapper = styled.nav`
background-color: white;
-webkit-box-shadow: 0 12px 15px 1px #f7f7f72e, 0 4.5px 1px 8px #ffffff2b, 0 11px 15px -4px #ffffff45;
box-shadow: 0 24px 38px 3px #f7f7f72e, 0 9px 46px 8px #ffffff2b, 0 11px 15px -7px #ffffff45;
border-top: 1px solid #114b74;
`;


